<template>
  <div class="com-homeuserinfo">
    <ul class="user-link" v-for="value in doList">
      <li class="user-icon">
        <router-link to="#" class="link-box ">
          <span class="txt">
            {{value.memberMobile}}  ({{value.memberNickname}})
            <span class="sex"> {{value.memberRole==2?'':''}} </span>
          </span>
          <img :src="baseUrl + value.memberAvatar">
        </router-link>
      </li>
      <li>
        <router-link to="#" class="forbid">
          <span>{{value.memberCreatetime}}</span>
          <span class="forbidAction">
            <span @click="permit(value.memberId)">  <span class="icon iconfont icon-wancheng"></span></span>
            <span @click="forbit(value.memberId)">  <span class="icon iconfont icon-cuowu"></span></span>
          </span>
          </router-link>
      </li>
    </ul>
  </div>
</template>
<script>
  import axios from 'axios'

  export default{
    data: function () {
      return {
        doList:'',
        baseUrl: 'http://www.sherlochao.com:9091/filebase'
      }
    },
    created: function () {
      this.$store.commit('changeIndexConf', {
        isFooter: true,
        isSearch: false,
        isBack: true,
        isShare: false,
        title: ''
      })
      this.fetchData()
    },
    methods:{
      fetchData:function () {
        let vm = this
        let url = 'http://www.sherlochao.com:9091/photosharing/administratirapi/listMember'
        axios.get(url).then(function (res) {
          vm.doList = res.data.data
        }).catch(function (error) {
          console.log(error)
        })
      },
      forbit:function (id) {
        let url = 'http://www.sherlochao.com:9091/photosharing/administratirapi/authorityMember'
        axios.get(url,{
          params: {
            memberId: id,
            choice: 2
          }
        }).then(function (res) {
          if(res.data.result === 1){
            alert(res.data.msg)
          }
        }).catch(function (error) {
          console.log(error)
        })
      },
      permit:function (id) {
        let url = 'http://www.sherlochao.com:9091/photosharing/administratirapi/authorityMember'
        axios.get(url,{
          params: {
            memberId: id,
            choice: 1
          }
        }).then(function (res) {
          if(res.data.result === 1){
            alert(res.data.msg)
          }
        }).catch(function (error) {
          console.log(error)
        })
      }

    }
  }
</script>
<style lang="scss">
  /*font-size*/
  $font:16;
  /**/
  $screen:750;
  /**/
  $bColor: #f9696c;
  $fontC:#666;
  @function px2rem($n){
  @return #{$n/($screen*$font/320)}rem
  }
  .com-homeuserinfo{
  .user-icon{
    height: px2rem(150);
    line-height: px2rem(150);
  img{
    position: absolute;
    right: px2rem(60);
    width: px2rem(100);
    height: px2rem(100);
    border-radius: 100%;
    top:px2rem(25);
  }

  }
  p{
    position: absolute;
    right: px2rem(60);
    color: #999;
    font-size: px2rem(32);
    top:0;
    line-height: px2rem(90);
  }
  }
  .forbid{
    font-size: .5rem;
    color: gray;

    .forbidAction{
      float:right;

      >span{
         color:red;
         font-size: .8rem;
         padding: 0px 5px;

        >span{
           font-size: .8rem;
         }
     }
    }
  }
  .sex{
    float: right;
  }
</style>



