import Vue from 'vue'
import router from './router/router'
import store from './store/store'
import VueResource from 'vue-resource'

Vue.use(VueResource)

Vue.config.debug = true
window.log = console.log
//  Vue.use() 
// VueResourceaxios

let data = {
  router,
  store
}

new Vue(data).$mount('#app')



